<style type="text/scss" lang="scss">
  .page-zdsy {
    /*height: 100%;*/
    background: #fff;
    .page-zdsy-header{
      padding: 0 .205rem;
      height: 1.18rem;
      border-bottom: 1px solid #eee;
      .img{
        width: .5rem;
        height: .5rem;
        border-radius:100%;
        background: #FFAD2C;
        .iconfont{
          flex: 1;
          text-align: center;
          font-size: .235rem;
          color: #fff;
        }
      }
      .info{
        margin-left: .195rem;
        h5{
          font-size:.12rem;
          color:#888;
          font-weight: normal;
        }
        span{
          font-size: .24rem;
          color: #333;
        }
      }
    }
    .czy-list{
      margin-bottom: 0.7rem;
    }
  }
</style>
<template>
    <div class="page-zdsy">
      <div class="page-zdsy-header am-flexbox">
        <div class="img am-flexbox">
          <i :class="zdsy.iconCls"></i>
        </div>
        <div class="am-flexbox-item info">
          <h4>{{zdsy.title}}</h4>
          <span>{{zdsy.se}}</span>
        </div>
        <i class="iconfont icon-rili" @click="openDate"></i>
    </div>
      <div class="czy-list-title">{{zdsy.subtitle}}</div>
      <div class="czy-list">
        <ul>
          <li v-for='(item, index) in zdsy.mxList' :key="index" >
            <div v-if="index===7" >
              <router-link :to="{name: 'page-zdsyList-zdsy-qtxsz'}" class="am-flexbox">
                <label class="am-flexbox-item">{{item.text}}</label>
                <i class="iconfont icon-jiantou"></i>
              </router-link>
            </div>
            <div class="am-flexbox" v-else>
              <label class="am-flexbox-item">{{item.text}}</label>
              <div >{{item.value}}</div>
            </div>
          </li>
        </ul>
      </div>
      <mt-datetime-picker
        ref="picker"
        type="date"
        v-model="pickerValue">
      </mt-datetime-picker>
    </div>
</template>
<script>
import { DatetimePicker } from 'mint-ui';
import Vue from 'vue';
Vue.component(DatetimePicker.name, DatetimePicker);
export default {
  name: 'czy-zdsy',
  data () {
    return {
      pickerValue: '',
      zdsy: {}
    };
  },
  methods: {
    openDate () {
      this.$refs.picker.open();
    },
    collect () {
      alert('收藏')
    },
    initPage () {
      //      this.$http.get('../../../../static/data/zdsy.json').then((res) => {
      //        res = res.body;
      //        this.zdsy = res.zdsy;
      //      }).catch((msg) => {
      //        console.log(msg);
      //      });
      this.zdsy = {
        'iconCls': 'iconfont icon-qian2',
        'title': '2018年税收收入',
        'se': '8123,876.89',
        'subtitle': '2018年重点税源明细表(单位：万)',
        'mxList': [
          {'text': '地方财政收入', 'value': '212,231.223'},
          {'text': '企业所得税', 'value': '112,344.232'},
          {'text': '增值税', 'value': '23.231.187'},
          {'text': '个人所得税', 'value': '23.231.187'},
          {'text': '城市维护建设税', 'value': '23.231.187'},
          {'text': '印花税', 'value': '23.231.187'},
          {'text': '房产税', 'value': '23.231.187'},
          {'text': '其他小税种', 'value': '23.231.187'}
        ]
      };
    },
  },
  created () {
    this.initPage();

    // 设置右上角按钮属性
    MyAlipayJSBridge('showOptionMenu');
    MyAlipayJSBridge('setOptionMenu', {
      title: '收藏'
    });
    MyAlipayJSBridge('optionMenu', {type: 'add'}, this.collect);
  },
  beforeRouteLeave (to, from, next) {
    MyAlipayJSBridge('optionMenu', {type: 'remove'}, this.collect);
    next();
  }
};
</script>



